<template>
	<el-row :gutter="20">
		<el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6" v-for="(item,index) in play" :key="index">
			<!-- <img :src="img" alt=""> -->
			<div @click="toDetail(item.id,index)">
				<el-card :body-style="{ padding: '20px 20px' }" >
					  <img style="width: 100%;height: 180px;" :src="item.url==' '?img:item.url" class="image">
					  <div style="height: 20px;"></div>
					  <div style="padding: 14px;">
						<span class="dd">{{item.title}}</span>
						<div style="height: 20px;"></div>
						<div class="bottom clearfix">
						  <time class="time">{{item.time}}</time>
						</div>
						<div style="height: 20px;"></div>
						<div class="bottom clearfix dd">
						  <time class="content" v-html="getNo(item.content)"></time>
						</div>
					  </div>
					  <div style="height: 20px;"></div>
					  <div style="float: right;"><i class="el-icon-right font-lg animate__animated " :class="isClass?'animate__bounceInLeft':''" @mouseover="addclass()"/></div>
					  <div style="height: 20px;"></div>
				</el-card>
			</div>
			<div style="height: 20px;"></div>
		</el-col>
			  
	</el-row>

</template>

<script>
	export default{
		props: {
			play: {
				type: [Array,Object],
			},
		},
		data() {
			return {
				isClass: false,
				img:require('@/assets/image/moren.jpg')
			}
		},
		methods: {
			
			addclass() {
				this.isClass = true
			},
			toDetail(id,index){
				// this.$A.get("/api/querydetail",{params: {id: id}})
				// .then(res=>{
				// 	console.log(res.data.data)
				// })
				// console.log(id,index)
				this.$router.push({ path: 'detail', query: { id: id }})
				// console.log(this.$route)
				// this.$router.push("/detail/:id")
				// let cot = this.play[index]
				// cot.content = cot.content.toString("UTF-8")
				// this.$router.push({ name: 'Detail', query: { title: cot.title ,content:cot.content,time:cot.time}})
			},
			
			getNo(str){
			   var reg = /<img src="(.+?)\">/g
				   // var reg = /\$\{(.+?)\}/g
			   var list = str.replace(reg,'')
			   return list
			},
		},
		// mounted:{
		// 	console.log(this.play)
		// }
		
	}
</script>

<style>
	.time{
		font-size: 12px;
		color: rgb(155, 155, 155);
		font-family: 微软雅黑;
		padding-top: 20px;
		cursor: pointer;
	}
	.dd{
		 width: 100%;
		  overflow: hidden;
		  text-overflow: ellipsis;
		  display: -webkit-box;
		  -webkit-line-clamp: 3;
		  -webkit-box-orient: vertical;
	}
	
</style>

